:root {
  --border-color: rgb(201, 201, 201);
}
.fondo {
  height: 100vh;
  background: url("./assets/headphones.png");
  background-repeat: no-repeat;
  background-size: 50%;
  background-position-x: right;
  max-width: 1300px;
  margin: auto;
}
.content {
  width: 90%;
  padding: 5%;
}

#buscar {
  margin-top: 60%;
  margin-bottom: 30px;
}

.search {
  font-size: 1.5rem;
  position: absolute;
  height: 40px;
  display: flex;
  align-items: center;
  margin-left: 6px;
  color: var(--border-color);
}

#buscador {
  height: 40px;
  width: 100%;
  background-color: transparent;
  border-radius: 20px;
  outline: none;
  padding: 6px;
  padding-left: 37px;
  font-size: 0.9rem;
  box-sizing: border-box;
  border: 1px solid var(--border-color);
  color: black;
  font-weight: bold;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 3px !important;
}

.swiper-slide {
  box-sizing: border-box;
  color: white;
  height: 46vw;
  background-color: blueviolet;
  border-radius: 8px;
  display: flex;
  align-items: flex-end;
  justify-content: flex-end;
  padding: 15px;
  -webkit-box-shadow: 0px 79px 47px -81px rgba(0, 0, 0, 0.75);
  -moz-box-shadow: 0px 79px 47px -81px rgba(0, 0, 0, 0.75);
  box-shadow: 0px 79px 47px -81px rgba(0, 0, 0, 0.75);
  font-size: 0.8rem;
}
.swiper-container {
  padding-bottom: 40px;
}
.fav {
  position: sticky !important;
  position: -webkit-sticky !important;
  top: 0; /* required */
}
a {
  text-decoration: none;
  color: black;
}

.loader2,
.loader2:before,
.loader2:after {
  border-radius: 50%;
  width: 2.5em;
  height: 2.5em;
  -webkit-animation-fill-mode: both;
  animation-fill-mode: both;
  -webkit-animation: load7 1.8s infinite ease-in-out;
  animation: load7 1.8s infinite ease-in-out;
}
.loader2 {
  color: blueviolet;
  font-size: 10px;
  margin: 80px auto;
  position: relative;
  text-indent: -9999em;
  -webkit-transform: translateZ(0);
  -ms-transform: translateZ(0);
  transform: translateZ(0);
  -webkit-animation-delay: -0.16s;
  animation-delay: -0.16s;
}
.loader2:before,
.loader2:after {
  content: "";
  position: absolute;
  top: 0;
}
.loader2:before {
  left: -3.5em;
  -webkit-animation-delay: -0.32s;
  animation-delay: -0.32s;
}
.loader2:after {
  left: 3.5em;
}
@-webkit-keyframes load7 {
  0%,
  80%,
  100% {
    box-shadow: 0 2.5em 0 -1.3em;
  }
  40% {
    box-shadow: 0 2.5em 0 0;
  }
}
@keyframes load7 {
  0%,
  80%,
  100% {
    box-shadow: 0 2.5em 0 -1.3em;
  }
  40% {
    box-shadow: 0 2.5em 0 0;
  }
}

@media screen and (min-width: 600px) {
  .fondo {
    background: none;
  }
  #buscar {
    margin-top: 0px;
    margin-bottom: 0px;
  }
  .swiper-slide {
    height: 20vh;
  }
}
